@use "../../../css/base/variables" as *;

.photo {
  position: relative;
  padding: 15px;
  margin: 15px 15px 0 15px;
  border-radius: 15px;
  background-color: $lred;
  line-height: 2rem;
  color: $dred;
  > * {
    margin-bottom: 8px;
  }
  &__img {
    display: block;
    width: 100%;
    height: auto;
  }
  &__desc {
    font-size: 1.5rem;
  }
}
